<template>
  <div class="d-flex flex-column align-center justify-center overview">
    <center><div ref="name" class="text-h2">GHEMID Mohamed</div></center>
    <div ref="position" class="text-h4 mb-8 text--secondary">
      Software Developer
    </div>
    <v-row>
      <v-col v-if="!isMobile" class="d-flex flex-column justify-space-between">
        <center>
          <v-icon color="cyan" large>mdi-package-variant-closed</v-icon>
          <div class="text-h5">Projects</div>
          <div>
            <input
              class="projectCounter mb-8"
              ref="projectCounter"
              value="0"
              readonly
            />
          </div>
        </center>

        <center ref="webSkill">
          <v-icon ref="webIcon" color="cyan" large>mdi-web</v-icon>
          <div class="text-h5">Web Development</div>
          <div class="text-h6 text--secondary mb-8">skills</div>
        </center>

        <center ref="mobileSkill">
          <v-icon color="cyan" large>mdi-cellphone-cog</v-icon>
          <div class="text-h5">Mobile Development</div>
          <div class="text-h6 text--secondary mb-8">skills</div>
        </center>
      </v-col>
      <v-col>
        <img height="500" ref="centerImg" src="@/assets/programmer.webp" />
      </v-col>

      <v-col
        cols="12"
        v-if="isMobile"
        class="d-flex flex-column justify-space-between"
      >
        <center>
          <v-icon color="cyan" large>mdi-package-variant-closed</v-icon>
          <div class="text-h5">Projects</div>
          <div>
            <input
              class="projectCounter mb-8"
              ref="projectCounter"
              value="0"
              readonly
            />
          </div>
        </center>
        <center ref="webSkill">
          <v-icon color="cyan" large>mdi-web</v-icon>
          <div class="text-h5">Web Development</div>
          <div class="text-h6 text--secondary mb-8">skills</div>
        </center>
        <center ref="mobileSkill">
          <v-icon color="cyan" large>mdi-cellphone-cog</v-icon>
          <div class="text-h5">Mobile Development</div>
          <div class="text-h6 text--secondary mb-8">skills</div>
        </center>
      </v-col>

      <v-col class="d-flex flex-column justify-space-between">
        <center>
          <v-icon color="cyan" large>mdi-school-outline</v-icon>
          <div class="text-h5">Diplomas</div>
          <div>
            <input
              class="diplomaCounter mb-8"
              ref="diplomaCounter"
              value="0"
              readonly
            />
          </div>
        </center>

        <center ref="embeddedSkill">
          <v-icon color="cyan" large>mdi-memory</v-icon>
          <div class="text-h5">Embedded Systems</div>
          <div class="text-h6 text--secondary mb-8">knowledge</div>
        </center>

        <center ref="designSkill">
          <v-icon color="cyan" large>mdi-material-design</v-icon>
          <div class="text-h5">UI/UX</div>
          <div class="text-h6 text--secondary mb-8">knowledge</div>
        </center>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import anime from "animejs";
import projects from "../data/projectsData";
import career from "../data/careerData";

export default {
  mounted() {
    this.animate();
  },
  updated() {
    this.animate();
  },
  methods: {
    animate() {
      this.timelineAnimation = anime.timeline({
        easing: "easeOutExpo",
      });
      this.timelineAnimation.add({
        targets: this.$refs.name,
        translateY: [-100, 0],
      });
      this.timelineAnimation.add(
        {
          targets: this.$refs.position,
          translateY: [1000, 0],
        },
        "-=500"
      );
      this.timelineAnimation.add(
        {
          targets: this.$refs.centerImg,
          translateY: [1000, 0],
          duration: 2000,
        },
        "-=600"
      );
      this.timelineAnimation.add(
        {
          targets: [this.$refs.webSkill, this.$refs.mobileSkill, this.$refs.projectCounter],
          translateX: [-1000, 0],
        },
        "-=600"
      );
      this.timelineAnimation.add(
        {
          targets: [this.$refs.embeddedSkill, this.$refs.designSkill, this.$refs.diplomaCounter],
          translateX: [1000, 0],
        },
        "-=600"
      );

      this.timelineAnimation.add(
        {
          targets: this.$refs.projectCounter,
          value: [0, projects.length],
          round: 1,
          easing: "easeInOutExpo",
          duration: 3000,
        },
        "-=1000"
      );

      const diplomaNumber = career.filter(
        (elt) => elt.type === "education" || elt.type === "certification"
      ).length;
      this.timelineAnimation.add(
        {
          targets: this.$refs.diplomaCounter,
          value: [0, diplomaNumber],
          round: 1,
          easing: "easeInOutExpo",
          duration: 3000,
        },
        "-=3100"
      );

      this.timelineAnimation.add(
        {
          targets: this.$refs.webIcon.$el,
          rotateY: 360,
        },
        "-=600"
      );
    },
  },
  data() {
    return {
      timelineAnimation: null,
    };
  },
  computed: {
    isMobile() {
      return this.$vuetify.breakpoint.xs;
    },
  },
};
</script>

